<template>
    <div class="content-bg">
        <div class="top-title">端口流量
            <v-flowchartfrom class="from-wapper" refreshBus="portFlowRefreshBus" dataBus="portFlowDataBus"></v-flowchartfrom>
        </div>
        <div class="item-wapper">
          <div class="item-title">目的端口总流量</div>
          <div class="item-content">
            <v-echarts echartType="bar" :echartData = "this.targetPortFlowData" ></v-echarts>
          </div>
        </div>
        <div class="item-wapper">
          <div class="item-title">目的端口总流量</div>
          <div class="item-content">
            <v-echarts echartType="pie" :echartData = "this.targetPortFlowData2" ></v-echarts>
          </div>
        </div>
        <div class="item-wapper">
          <div class="item-title">TCP目的端口总流量</div>
          <div class="item-content">
            <v-echarts echartType="bar" :echartData = "this.TCPPortFlowData" ></v-echarts>
          </div>
        </div>
        <div class="item-wapper">
          <div class="item-title">TCP目的端口总流量</div>
          <div class="item-content">
            <v-echarts echartType="pie" :echartData = "this.TCPPortFlowData2" ></v-echarts>
          </div>
        </div>
        <div class="item-wapper">
          <div class="item-title">UDP目的端口总流量</div>
          <div class="item-content">
            <v-echarts echartType="bar" :echartData = "this.UDPPortFlowData" ></v-echarts>
          </div>
        </div>
        <div class="item-wapper">
          <div class="item-title">UDP目的端口总流量</div>
          <div class="item-content">
            <v-echarts echartType="pie" :echartData = "this.UDPPortFlowData2" ></v-echarts>
          </div>
        </div>
    </div>
    
</template>

<script>
    import vFlowchartfrom from '../common/flowChartsFrom'
    import vEcharts from '../common/echarts'
    import bus from '../common/bus';
    import {dateFormat} from '../../../static/js/common'
    export default {
        name: "portFlow",
        data() {
            return {
                interTime:'',
                //刷新时间间隔
                refreshIntTime :'5000',
                //数据日期间隔
                dataTime:3600,
                //目的端口总流量
                targetPortFlowData:{
                    baseConfig:{
                        title:'',
                        xAxisName:'端口',
                        yAxisName:'数据包/个',
                        rotate:'25',
                        itemColor:['rgba(68,47,148,0.5)','rgba(15,219,243,1)']
                    },
                    xAxisArr:[],
                    yAxisArr:[]
                },
                targetPortFlowData2:{
                    baseConfig:{
                        title:'',
                        hoverText:'百分比'
                    },
                    xAxisArr:[],
                    yAxisArr:[]
                },
                //TCP端口总流量
                TCPPortFlowData:{
                    baseConfig:{
                        title:'',
                        xAxisName:'端口',
                        yAxisName:'数据包/个',
                        rotate:'25',
                        itemColor:['rgba(68,47,148,0.5)','rgba(15,219,243,1)']
                    },
                    xAxisArr:[],
                    yAxisArr:[]
                },
                TCPPortFlowData2:{
                    baseConfig:{
                        title:'',
                        hoverText:'百分比'
                    },
                    xAxisArr:[],
                    yAxisArr:[]
                },
                //TCP端口总流量
                UDPPortFlowData:{
                    baseConfig:{
                        title:'',
                        xAxisName:'端口',
                        yAxisName:'数据包/个',
                        rotate:'25',
                        itemColor:['rgba(68,47,148,0.5)','rgba(15,219,243,1)']
                    },
                    xAxisArr:[],
                    yAxisArr:[]
                },
                UDPPortFlowData2:{
                    baseConfig:{
                        title:'',
                        hoverText:'百分比'
                    },
                    xAxisArr:[],
                    yAxisArr:[]
                }
            }
        },
        created(){
            /*监听刷新时间改变*/
            bus.$on('portFlowRefreshBus',(val)=>{
                    this.refreshIntTime = val;
                    clearInterval(this.interTime);
                    this.setInterFun();
            })
            /*监听数据时间改变*/
            bus.$on('portFlowDataBus',(val)=>{
                this.dataTime = val;
            })

        },
        mounted(){
            //第一次获取数据
            /*获取目的端口总流量*/
            this.getTargetPortFlowData(this.dataTime);
            /*获取TCP端口总流量*/
            this.getTCPPortFlowData(this.dataTime);
            /*获取UDP端口总流量*/
            this.getUDPPortFlowData(this.dataTime);

        },
        methods:{
            /*获取目的端口总流量*/
            getTargetPortFlowData(dataTime){
                this.$nextTick(()=>{

                    this.$post('/flow/getDstPortCount.do',{
                      timeInterval:dataTime
                    })
                        .then(res=>{
                            let xns1 = [];
                            let yvs1 = [];
                            let yvs2 = [];
                            for(let i in res){
                              let obj = res[i];
                              for (let j in obj){
                                xns1.push(j);
                                yvs1.push(obj[j]);
                                yvs2.push({
                                  name:j,
                                  value:obj[j]
                                })
                              }
                            }
                            this.targetPortFlowData.xAxisArr = xns1;
                            this.targetPortFlowData.yAxisArr = yvs1;
                            this.targetPortFlowData2.yAxisArr = yvs2;
                        })
                        .catch(err=>{

                        })
                })
            },
            /*获取TCP目的端口总流量*/
            getTCPPortFlowData(dataTime){
                this.$nextTick(()=>{

                    this.$post('/flow/getTCPDstPortCount.do',{
                      timeInterval:dataTime
                    })
                        .then(res=>{
                            let xns1 = [];
                            let yvs1 = [];
                            let yvs2 = [];
                            for(let i in res){
                              let obj = res[i];
                              for (let j in obj){
                                xns1.push(j);
                                yvs1.push(obj[j]);
                                yvs2.push({
                                  name:j,
                                  value:obj[j]
                                })
                              }
                            }
                            this.TCPPortFlowData.xAxisArr = xns1;
                            this.TCPPortFlowData.yAxisArr = yvs1;
                            this.TCPPortFlowData2.yAxisArr = yvs2;
                        })
                        .catch(err=>{

                        })
                })
            },
            /*获取UDP端口总流量*/
            getUDPPortFlowData(dataTime){
                this.$nextTick(()=>{
                    this.$post('/flow/getUDPDstPortCount.do',{
                      timeInterval:dataTime
                    })
                        .then(res=>{
                            let xns1 = [];
                            let yvs1 = [];
                            let yvs2 = [];
                            for(let i in res){
                              let obj = res[i];
                              for (let j in obj){
                                xns1.push(j);
                                yvs1.push(obj[j]);
                                yvs2.push({
                                  name:j,
                                  value:obj[j]
                                })
                              }
                            }
                            this.UDPPortFlowData.xAxisArr = xns1;
                            this.UDPPortFlowData.yAxisArr = yvs1;
                            this.UDPPortFlowData2.yAxisArr = yvs2;
                        })
                        .catch(err=>{

                        })
                })
            },
            /*计时器*/
            setInterFun(){
              /*循环获取数据*/
              this.interTime = setInterval(()=>{
                /*获取目的端口总流量*/
                this.getTargetPortFlowData(this.dataTime);
                /*获取TCP端口总流量*/
                this.getTCPPortFlowData(this.dataTime);
                /*获取UDP端口总流量*/
                this.getUDPPortFlowData(this.dataTime);
              },this.refreshIntTime);
            }
        },
        beforeRouteEnter(to, from, next){
          next(vm => {
            vm.setInterFun();
          });
        },
        beforeRouteLeave(to, from, next) {
          clearInterval(this.interTime);
          next();
        },
        components:{
            vFlowchartfrom,
            vEcharts
        }
    }
</script>

<style scoped>
  .content-bg{
    /*height: 1000px;*/
    font-size: 1rem;
    background: url("../../../static/img/flow-index-bg2.png");
    background-size: 100% 100%;
  }
  .top-title{
    font-size: 0.5rem;
    color: #185bff;
    padding: 10px;
  }
  .item-wapper{
    height: auto;
    /*background: rgb(26,36,47);*/
    background: url("../../../static/img/bd2.png");
    background-size: 100% 100%;
    margin:0.5rem 0.2rem;
  }
  .item-title{
    height: 1rem;
    line-height: 1rem;
    font-size: 0.38rem;
    text-align: left;
    padding-left: 25px;
    /*background: url("../../../static/img/title-bg.png");*/
    background-size: 100% 100%;
  }
  .item-content{
    /*height: calc(100% - 1rem);*/
    height: 230px;
    box-sizing: border-box;
    padding: 0.5rem;
    padding-top: 0;
  }
</style>



