<template>
    <div class="content-bg">
      <div class="top-title">全局实时流量
        <v-flowchartfrom class="from-wapper" refreshBus="realTimeFlowRefreshBus" :refreshObj="refreshObj"></v-flowchartfrom>
      </div>
      <div class="item-wapper">
        <div class="item-title">全局-利用率百分比</div>
        <div class="chart-from">
          <span >带宽：</span>
          <el-select v-model="bandwidth" placeholder="请选择" size="mini" style="width: 80px;">
            <el-option
              v-for="item in bandwidthArr"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </div>
        <div class="item-content">
          <v-echarts echartType="timeline" :echartData = "this.allUsedData" ></v-echarts>
        </div>
        <div class="item-content">
          <v-echarts echartType="gauge" :echartData = "this.allUsedData2" ></v-echarts>
        </div>
      </div>
      <div class="item-wapper">
        <div class="item-title">全局数据包个数</div>
        <div class="item-content">
          <v-echarts echartType="timeline" :echartData = "this.allPacketsData" ></v-echarts>
        </div>
      </div>
    </div>
    
</template>

<script>
    import vFlowchartfrom from '../common/flowChartsFrom'
    import vEcharts from '../common/echarts'
    import bus from '../common/bus';
    import {dateFormat} from '../../../static/js/common'
    export default {
        name: "realTimeFlow",
        data() {
            return {
                interTime:'',
                refreshObj:{
                  defaultVal:'5',
                  opt:[{
                    label:'5s',
                    value:'5'
                  },
                    {
                      label:'10s',
                      value:'10'
                    },
                    {
                      label:'20s',
                      value:'20'
                    }
                  ]
                },
                //刷新时间间隔
                refreshIntTime :'5000',
                //数据日期间隔
                dataTime:1,
                //全局利用率百分比
                bandwidth:10,
                bandwidthArr:[
                    {
                        label:'10M',
                        value:10
                    },
                    {
                        label:'100M',
                        value:100
                    },
                    {
                        label:'1000M',
                        value:1000
                    }
                ],
                allUsedData:{
                    baseConfig:{
                        title:'',
                        xAxisName:'时间',
                        yAxisName:'百分比/%',
                        hoverText:'',
                    },
                    yAxisArr:[{
                        name:'',
                        color:'rgb(15,219,243)',
                        data:[]
                    }]
                },
                allUsedData2:{
                    baseConfig:{
                        title:'',
                        xAxisName:'',
                        yAxisName:'',
                        hoverText:'全局',
                    },
                    yAxisArr:[{
                        name:'利用率',
                        data:[/*{value: 10, name: '利用率'}*/]
                    }]
                },
                //全局数据包个数
                allPacketsData:{
                    baseConfig:{
                        title:'',
                        xAxisName:'时间',
                        yAxisName:'个数/个',
                        hoverText:'',
                    },
                    yAxisArr:[{
                        name:'',
                        color:'rgb(15,219,243)',
                        data:[]
                    }]
                }
            }
        },
        created(){
            /*监听刷新时间改变*/
            bus.$on('realTimeFlowRefreshBus',(val)=>{
                    this.refreshIntTime = val*1000;
                    clearInterval(this.interTime);
                    this.setInterFun();
            })
            /*监听数据时间改变*/
            bus.$on('realTimeFlowDataBus',(val)=>{
                this.dataTime = val;
            })

        },
        mounted(){
            //第一次获取数据
            let endtime = dateFormat('yyyy-mm-dd HH:MM:SS',new Date());
            let st = new Date(new Date(endtime).valueOf() - this.dataTime * 60 * 60 * 1000);
            let starttime = dateFormat('yyyy-mm-dd HH:MM:SS',st);
            /*获取全局利用率百分比*/
            this.getAllUsedData(this.refreshIntTime/1000);
            /*获取全局数据包个数*/
            this.getAllPacketsData(this.refreshIntTime/1000);
            /*循环获取数据*/

        },
        methods:{
            /*获取全局利用率百分比*/
            getAllUsedData(timeInterval){
                this.$nextTick(()=>{
                    this.$post('/flow/getPacketLengthPerSecond.do',{
                      timeInterval:timeInterval
                    })
                        .then(res=>{
                            res[0].value[1] = res[0].value[1] * 8 * 1000 * 100/ this.refreshIntTime / this.bandwidth / 1024;
                            res[0].value[1] = res[0].value[1].toFixed(2);
                            if(this.allUsedData.yAxisArr[0].data.length < 60){
                                this.allUsedData.yAxisArr[0].data.push(res[0])
                            }else{
                                this.allUsedData.yAxisArr[0].data.shift();
                                this.allUsedData.yAxisArr[0].data.push(res[0])
                            }
                            this.allUsedData2.yAxisArr[0].data=[{
                                value:res[0].value[1],
                                name:'利用率'
                            }]
                        })
                        .catch(err=>{

                        })
                })
            },
            /*获取全局数据包个数*/
            getAllPacketsData(timeInterval){
                this.$nextTick(()=> {
                    this.$post( '/flow/getPacketCount.do',{
                      timeInterval:timeInterval
                    })
                        .then(res => {
                            if (this.allPacketsData.yAxisArr[0].data.length < 60) {
                                this.allPacketsData.yAxisArr[0].data.push(res[0]);
                            } else {
                                this.allPacketsData.yAxisArr[0].data.shift();
                                this.allPacketsData.yAxisArr[0].data.push(res[0]);
                            }
                        })
                        .catch(err => {

                        })
                })
            },
            /*计时器*/
            setInterFun(){
              this.interTime = setInterval(()=>{
                let endtime = dateFormat('yyyy-mm-dd HH:MM:SS',new Date());
                let st = new Date(new Date(endtime).valueOf() - this.dataTime * 60 * 60 * 1000);
                let starttime = dateFormat('yyyy-mm-dd HH:MM:SS',st);
                /*获取全局利用率百分比*/
                this.getAllUsedData(this.refreshIntTime/1000);
                /*获取全局数据包个数*/
                this.getAllPacketsData(this.refreshIntTime/1000);
              },this.refreshIntTime);
            }
        },
        beforeRouteEnter(to, from, next){
          next(vm => {
            vm.setInterFun();
          });
        },
        beforeRouteLeave(to, from, next) {
          clearInterval(this.interTime);
          next();
        },
        components:{
            vFlowchartfrom,
            vEcharts
        }
    }
</script>

<style scoped>
  .content-bg{
    /*height: 1000px;*/
    font-size: 1rem;
    background: url("../../../static/img/flow-index-bg2.png");
    background-size: 100% 100%;
  }
  .top-title{
    font-size: 0.5rem;
    color: #185bff;
    padding: 10px;
  }
  .chart-from{
    position: absolute;
    right: 26px;
    top: 3px;
    height: 35px;
    line-height: 35px;
    display: flex;
    align-items: center;
  }
  .chart-from span{
    font-size: 0.35rem;
  }
  .chart-from /deep/ .el-input--mini .el-input__inner{
    border: 1px solid #409eff;
    color: #409eff;
    background: 0;
    border-top: 0;
    border-left: 0;
    border-right: 0;
  }
  .item-wapper{
    position: relative;
    height: auto;
    /*background: rgb(26,36,47);*/
    background: url("../../../static/img/bd2.png");
    background-size: 100% 100%;
    margin:0.5rem 0.2rem;
  }
  .item-title{
    height: 1rem;
    line-height: 1rem;
    font-size: 0.38rem;
    text-align: left;
    padding-left: 25px;
    /*background: url("../../../static/img/title-bg.png");*/
    background-size: 100% 100%;
  }
  .item-content{
    /*height: calc(100% - 1rem);*/
    height: 230px;
    box-sizing: border-box;
    padding: 0.5rem;
    padding-top: 0;
  }
</style>



