<template>
    <div class="content-bg" v-loading.fullscreen.lock="fullscreenLoading" element-loading-text="获取中"  element-loading-background="rgba(0, 0, 0, 0.6)">
        <div class="top-title">全局端口流量</div>
        <div style="padding: 0 5px;"><v-basedate class="from-wapper" busName="portFlow2DateBus2"></v-basedate></div>
        <div class="item-wapper">
          <div class="item-title">目的端口总流量</div>
          <div class="item-content">
            <v-echarts echartType="bar" :echartData = "this.targetPortFlowData" ></v-echarts>
          </div>
        </div>
        <div class="item-wapper">
          <div class="item-title">目的端口总流量</div>
          <div class="item-content">
            <v-echarts echartType="pie" :echartData = "this.targetPortFlowData2" ></v-echarts>
          </div>
        </div>
        <div class="item-wapper">
          <div class="item-title">TCP目的端口总流量</div>
          <div class="item-content">
            <v-echarts echartType="bar" :echartData = "this.TCPPortFlowData" ></v-echarts>
          </div>
        </div>
        <div class="item-wapper">
          <div class="item-title">TCP目的端口总流量</div>
          <div class="item-content">
            <v-echarts echartType="pie" :echartData = "this.TCPPortFlowData2" ></v-echarts>
          </div>
        </div>
        <div class="item-wapper">
          <div class="item-title">UDP目的端口总流量</div>
          <div class="item-content">
            <v-echarts echartType="bar" :echartData = "this.UDPPortFlowData" ></v-echarts>
          </div>
        </div>
        <div class="item-wapper">
          <div class="item-title">UDP目的端口总流量</div>
          <div class="item-content">
            <v-echarts echartType="pie" :echartData = "this.UDPPortFlowData2" ></v-echarts>
          </div>
        </div>
    </div>
    
</template>

<script>
    import vBasedate from '../common/baseDate'
    import vEcharts from '../common/echarts'
    import bus from '../common/bus';
    import {dateFormat} from '../../../static/js/common'
    export default {
        name: "portFlow2",
        data() {
            return {
                fullscreenLoading:false,
                interTime:'',
                //刷新时间间隔
                refreshIntTime :'5000',
                //数据日期间隔
                dataTime:3600,
                //目的端口总流量
                targetPortFlowData:{
                    baseConfig:{
                        title:'',
                        xAxisName:'端口',
                        yAxisName:'数据包/个',
                        rotate:'25',
                        itemColor:['rgba(68,47,148,0.5)','rgba(15,219,243,1)']
                    },
                    xAxisArr:[],
                    yAxisArr:[]
                },
                targetPortFlowData2:{
                    baseConfig:{
                        title:'',
                        hoverText:'百分比'
                    },
                    xAxisArr:[],
                    yAxisArr:[]
                },
                //TCP端口总流量
                TCPPortFlowData:{
                    baseConfig:{
                        title:'',
                        xAxisName:'端口',
                        yAxisName:'数据包/个',
                        rotate:'25',
                        itemColor:['rgba(68,47,148,0.5)','rgba(15,219,243,1)']
                    },
                    xAxisArr:[],
                    yAxisArr:[]
                },
                TCPPortFlowData2:{
                    baseConfig:{
                        title:'',
                        hoverText:'百分比'
                    },
                    xAxisArr:[],
                    yAxisArr:[]
                },
                //TCP端口总流量
                UDPPortFlowData:{
                    baseConfig:{
                        title:'',
                        xAxisName:'端口',
                        yAxisName:'数据包/个',
                        rotate:'25',
                        itemColor:['rgba(68,47,148,0.5)','rgba(15,219,243,1)']
                    },
                    xAxisArr:[],
                    yAxisArr:[]
                },
                UDPPortFlowData2:{
                    baseConfig:{
                        title:'',
                        hoverText:'百分比'
                    },
                    xAxisArr:[],
                    yAxisArr:[]
                }
            }
        },
        created(){
            /*监听刷新时间改变*/
            bus.$on('portFlow2DateBus2',(arr)=>{
              let paramObj = {
                starttime:arr[0],
                endtime:arr[1]
              }
              this.fullscreenLoading = true;
              /*获取目的端口总流量*/
              this.getTargetPortFlowData(paramObj);
              /*获取TCP端口总流量*/
              this.getTCPPortFlowData(paramObj);
              /*获取UDP端口总流量*/
              this.getUDPPortFlowData(paramObj);
            })
        },
        mounted(){},
        methods:{
            /*获取目的端口总流量*/
            getTargetPortFlowData(paramObj){
                this.$nextTick(()=>{
                    this.$post('/flow/getDstPortCount.do',paramObj)
                        .then(res=>{
                            this.fullscreenLoading = false;
                            let xns1 = [];
                            let yvs1 = [];
                            let yvs2 = [];
                            for(let i in res){
                              let obj = res[i];
                              for (let j in obj){
                                xns1.push(j);
                                yvs1.push(obj[j]);
                                yvs2.push({
                                  name:j,
                                  value:obj[j]
                                })
                              }
                            }
                            this.targetPortFlowData.xAxisArr = xns1;
                            this.targetPortFlowData.yAxisArr = yvs1;
                            this.targetPortFlowData2.yAxisArr = yvs2;
                        })
                        .catch(err=>{
                          this.fullscreenLoading = false;
                        })
                })
            },
            /*获取TCP目的端口总流量*/
            getTCPPortFlowData(paramObj){
                this.$nextTick(()=>{
                    this.$post('/flow/getTCPDstPortCount.do',paramObj)
                        .then(res=>{
                            this.fullscreenLoading = false;
                            let xns1 = [];
                            let yvs1 = [];
                            let yvs2 = [];
                            for(let i in res){
                              let obj = res[i];
                              for (let j in obj){
                                xns1.push(j);
                                yvs1.push(obj[j]);
                                yvs2.push({
                                  name:j,
                                  value:obj[j]
                                })
                              }
                            }
                            this.TCPPortFlowData.xAxisArr = xns1;
                            this.TCPPortFlowData.yAxisArr = yvs1;
                            this.TCPPortFlowData2.yAxisArr = yvs2;
                        })
                        .catch(err=>{
                          this.fullscreenLoading = false;
                        })
                })
            },
            /*获取UDP端口总流量*/
            getUDPPortFlowData(paramObj){
                this.$nextTick(()=>{
                    this.$post('/flow/getUDPDstPortCount.do',paramObj)
                        .then(res=>{
                            this.fullscreenLoading = false;
                            let xns1 = [];
                            let yvs1 = [];
                            let yvs2 = [];
                            for(let i in res){
                              let obj = res[i];
                              for (let j in obj){
                                xns1.push(j);
                                yvs1.push(obj[j]);
                                yvs2.push({
                                  name:j,
                                  value:obj[j]
                                })
                              }
                            }
                            this.UDPPortFlowData.xAxisArr = xns1;
                            this.UDPPortFlowData.yAxisArr = yvs1;
                            this.UDPPortFlowData2.yAxisArr = yvs2;
                        })
                        .catch(err=>{
                          this.fullscreenLoading = false;
                        })
                })
            },
        },
        components:{
            vBasedate,
            vEcharts
        }
    }
</script>

<style scoped>
  .content-bg{
    /*height: 1000px;*/
    font-size: 1rem;
    background: url("../../../static/img/flow-index-bg2.png");
    background-size: 100% 100%;
  }
  .top-title{
    font-size: 0.5rem;
    color: #185bff;
    padding: 10px;
  }
  .item-wapper{
    height: auto;
    /*background: rgb(26,36,47);*/
    background: url("../../../static/img/bd2.png");
    background-size: 100% 100%;
    margin:0.5rem 0.2rem;
  }
  .item-title{
    height: 1rem;
    line-height: 1rem;
    font-size: 0.38rem;
    text-align: left;
    padding-left: 25px;
    /*background: url("../../../static/img/title-bg.png");*/
    background-size: 100% 100%;
  }
  .item-content{
    /*height: calc(100% - 1rem);*/
    height: 230px;
    box-sizing: border-box;
    padding: 0.5rem;
    padding-top: 0;
  }
</style>



